import React, { useEffect, useState } from 'react'
import './styles.css'

import { InputText } from 'primereact/inputtext';
import { ButtonBase } from '@mui/material';

import { useNavigate } from "react-router-dom";
import api from '../../services/api'

const LoginPage = () => {
    const navigate = useNavigate()

    useEffect(() => {
        if (localStorage.getItem("@greenMix:TOKENS")) {
            navigate(`/products`, {replace: true})
        }
    }, [])

    const [user, setUser] = useState({
        username: "",
        password: ""
    })

    const login = () => {
        api.post('/token/', user).then(res => {
            if (res.status === 200) {
                localStorage.setItem("@greenMix:TOKENS", JSON.stringify(res.data))
                localStorage.setItem("@greenMix:USERNAME", user.username)
                navigate(`/products`, {replace: true})
            }
        }).catch(err => {
            console.log(err)
            alert("Usurio ou senha invlidos")
        })
    }

    return (
        <div className='loginPage'>
            <div className='upper'>
                <div className='content'>
                    <img src="/assets/images/GreenMix.svg" alt="logo greenmix"/>
                    <InputText 
                        value={user.username} 
                        onChange={(e) => setUser({...user, username: e.target.value})}
                        placeholder="Nome de usurio"
                    />
                    <InputText 
                        value={user.password} 
                        onChange={(e) => setUser({...user, password: e.target.value})}
                        placeholder="Senha"
                        type="password"
                    />
                    <ButtonBase 
                        className='greenButton'
                        onClick={() => login()}
                    >Entrar</ButtonBase>
                </div>
            </div>
            <div className='lower'>
                <img id="login--img" src="/assets/images/Login_illustration.svg" alt="ilustrao de login"/>
            </div>
        </div>
    )
}

export default LoginPage